
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
}

nav {
  border-bottom: 4px solid black;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background: black;
  color: white;
}

#page-body {
  max-width: 62%;
  height: 100vh;
  margin: auto;
  padding-top: 64px;
}

.note-list {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
  font-weight: bold;
}

.note-list p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
  font-weight: 100;
}




